import React , {useState} from 'react';
import { Button} from '../ButtonsElements';
import { IndicatorContainer,IndicatorBtnWrapper ,ArrowForward,ArrowRight,StyledForm,StyledFormWrapper,StyledInput} from './IndicatorElements'

const IndicatorSection = () =>{
    const [Indicator,setIndicator]=useState('');
    const [Period,setPeriod]=useState('3m');
    const[hover,setHover]=useState(false)

    const handleSubmit=(e)=>{
        e.preventDefault();
    }

    const onHover=()=>{
        setHover(!hover)
    }
    return (
        <IndicatorContainer>
            <StyledFormWrapper>
                <StyledForm onSubmit={handleSubmit}>
                    <label>Indicator Name</label>
                    <StyledInput
                    type="text"
                    required
                    value={Indicator}
                    onChange={(e)=>setIndicator(e.target.value)}
                    />
                    <label>Indicator period</label>
                    <select
                        value={Period}
                        onChange={(e)=>setPeriod(e.target.value)}
                        >
                    <option value="1d">1day</option>
                    <option value="5d">5day</option>
                    <option value="1mo">1month</option>
                    <option value="3m">3month</option>
                    <option value="6mo">6month</option>
                    <option value="1y">1year</option>
                    <option value="5y">5year</option>
                    </select>
                    <p>{Indicator}</p> 
                    <p>{Period}</p>
                </StyledForm>
             </StyledFormWrapper>
            <IndicatorBtnWrapper> 
                <Button 
                onMouseEnter={onHover}
                onMouseLeave={onHover}
                primary= "true"
                dark="true"
                >
                Submit{hover ? <ArrowForward/>:<ArrowRight/>}
                </Button>
            </IndicatorBtnWrapper>
            </IndicatorContainer>
            
    );
}
export default IndicatorSection;